<template>
  <div id="app">
    <addtodo @addtodo="addtodo" />
    <todos :todos="todos" @deleteitem="deleteitem" />
  </div>
</template>
<script>
import todos from "../components/todos";
import axios from "axios";
import addtodo from "../components/addtodo";
export default {
  name: "app",
  components: {
    todos,
    addtodo
  },
  data() {
    return {
      todos: []
    };
  },
  methods: {
    deleteitem(title) {
        axios
        .delete((`http://jsonplaceholder.typicode.com/todos/${title}`))
      .then(res=>this.todos = this.todos.filter(todo => todo.title !== title))
    },
    addtodo(newtodo) {
      const { id, title, completed } = newtodo;
      axios
        .post("http://jsonplaceholder.typicode.com/todos", {
          id,
          title,
          completed
        })
        .then(res => {
          this.todos = [res.data, ...this.todos];
        });
    }
  },
  created() {
    axios
      .get("http://jsonplaceholder.typicode.com/todos?_limit=10")
      .then(res => {
        this.todos = res.data;
      })
      .catch(err => console.log(err));
  }
};
</script>
