<template>
  <form @submit.prevent="addtodo">
    <input type="text" v-model="title" placeholder="请输入待办事项" />
    <input type="submit" placeholder="提交" class="btn" />
  </form>
</template>
<script>
import uuid from "uuid";
export default {
  name: "addtodo",

  data() {
    return {
      title: ""
    };
  },
  methods: {
    addtodo() {
    const newtodo={
        id:uuid.v4(),
       title:this.title,
       completed:false 
    };
   this.$emit("addtodo",newtodo)
    }
  }
};
</script>
<style scoped>
form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 5px;
  border: 1px #999 solid;
}
input[type="submit"] {
  flex: 2;
}
</style>